body {
  --gap-between: 6px;
  --margin-bottom: 9px;
  --key-height: 45px;
  --grid-padding-top: 12px;
  --grid-padding-bottom: 24px;
  --grid-max-height: calc(var(--margin-bottom)*3 + var(--key-height)*3 + var(--grid-padding-bottom) + var(--grid-padding-top));
}

.game-grid {
  /* Must match the keyboard max-height */
  padding-bottom: var(--grid-max-height);
}

.container {
  margin-top: 0;
  margin-bottom: 0;
  display: flex;
  justify-content: center; 
  width: 100%;
}

.level-div {
  display: flex;
  justify-content: space-between; 
  align-items: center;
  width: 310px; 
  padding: 0 10px;
}

.level-div > p {
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 10px;
  margin-bottom: 0;
}

.icon-text {
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin: 0;
}

.icon-text > * {
  margin: 0;
  padding: 0;
}

.wrong-ico {
  color: red;
  margin-left: 10px;
}

.check-ico {
  color: green;
  margin-right: 10px;
}

.center-text {
  flex-grow: 1;
  text-align: center;  
}