body {
  --max-width-of-row: calc(calc(57+4+3) * 5); /*NUM_OF_LETTERS * (LETTER_BOX HEIGHT + MARGIN) not margin*2 to leave some space*/
}

.letter-box {
  width: 55px;
  height: 55px;
  font-size: 1.75rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 3px;
  transition: transform 0.5s linear, background-color 0.5s ease-in-out, border 0.5s linear;
  transform-style: preserve-3d;
  user-select: none;
}
.empty-letter {
  border: 2px solid rgb(58, 58, 60);
}
.wrong-letter {
  background-color: rgb(58, 58, 58);
  border: 2px solid rgb(58, 58, 58);
}
.guessed-letter {
  background-color: rgb(83, 141, 78);
  border: 2px solid rgb(83, 141, 78);
}
.half-letter {
  background-color: rgb(181, 159, 59);
  border: 2px solid rgb(181, 159, 59);
}

.letter-flip {
  transform: rotateX(360deg);
}

.letter {
  backface-visibility: hidden;
  user-select: none;
}

